import React, { Component } from "react";
import { Menu } from "antd";
import './menu.css';
class MenuLight extends Component {
  render() {
    return (
      <div className={this.props.className || "menu-light"}>
        <Menu {...this.props}>{this.props.children}</Menu>
      </div>
    );
  }
}
export default MenuLight;
