import React from 'react';
import { Menu } from "antd";
import './dropdownProject.css';

class Dropdown extends React.Component {
  constructor(props) {
    super(props);
    this.handleChange = this.handleChange.bind(this);
    this.state = {temperature: ''};
    this.theme = props.theme;
    this.lists = props.lists;
    this.cssClass = props.cssClass;
  }

  handleChange(e) {
    this.setState({temperature: e.target.value});
  }

  
  
  render() {

    return (
      <Menu theme={"light"}
            className={this.cssClass}>
        {
          this.lists.map(item => {
            let element;
            if(item.type === 'link'){
              element =  <Menu.Item key={this.lists.indexOf(item)}>{item.name}</Menu.Item>;
            }else if(item.type === 'divider'){
              element = <Menu.Divider  key={this.lists.indexOf(item)}/>;
      
            }else if(item.type === 'disabled'){
              element = <Menu.Item  key={this.lists.indexOf(item)} disabled className={item.class}>{item.name}</Menu.Item>;
            }else{
              element = <Menu.Item>type, name and link </Menu.Item>;
            }
            return element;
          })
        }
      </Menu>
    );
  }
}

export default Dropdown;


