import { Constants, Api } from "../utils";
import { parseFormData } from "../utils/Helper";
const qs = require("qs");

const reset_password = async params => {
  return Api.post(Constants.URL_SOLICITAR_CONTRASENA, parseFormData(params));
};
const change_password = async params => {
  return Api.post(Constants.URL_CAMBIAR_CONTRASENA, parseFormData(params));
};
const login = async params => {
  return new Promise((resolve, reject) => {
    /*  Api.defaults.xsrfHeaderName = "123";
    document.cookie = Api.defaults.xsrfCookieName + "=12345"; */
    Api.post(Constants.URL_LOGIN, params)
      .then(response => {
        let { data } = response;
        let { success, token } = data;
        if (success) {
          Api.defaults.headers.Authorization = token;
        }
        resolve(response);
      })
      .catch(err => reject);
  });
};
const list_frias = async params => {
  return Api.get(`${Constants.URL_BASE}/admin/frias`);
};

export { login, reset_password, change_password, list_frias };
