import React, { Component } from "react";
/* Redux Connect */
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import actions from "../../../redux/actions";

import { CodeEditor } from "../../../components";

class cpanelDashboard extends Component {
  componentDidMount() {
    const { actions } = this.props;
    this.themeColor = this.props.match.params.theme;
    actions.action_search(["1", "2", "3"]);
  }
  componentWillReceiveProps(nextProps) {
    let { data } = nextProps;
    console.log("Example Reducers Data: ", data);
  }
  render() {
    return (
      <div className="home-container">
        <div>
          <h1>Hola Mundo!, soy el dashboard donecLab</h1>
          <CodeEditor config={JSON.stringify({})} />
        </div>
      </div>
    );
  }
}
/* Mapping Actions to Props*/
const mapStateToProps = state => {
  const { data } = state;
  return {
    data
  };
};
/* Event Action Dispatch to Props */
const mapDispatchToProps = dispatch => ({
  actions: bindActionCreators(actions, dispatch)
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(cpanelDashboard);
