import React from "react";
import { withRouter } from "react-router-dom";
import { Layout } from "antd";
import qs from "qs";
import HeaderConfig from "../../components/header/config/configHeader";
import Footer from "../../components/footer/Footer";
import "./configLayout.scss";

const { Content } = Layout;
const Logo = require("../../resources/images/logo.svg");

const ConfigLayout = props => {
  let { className, children, location } = props;
  let { search } = location;
  let query = qs.parse(search.replace("?", ""));
  let theme = (query.theme !== 'dark' && query.theme !== 'light') ? "light" : query.theme ;
  return (
    <Layout className={`main-layout ${className || ""} ${theme}`} >
        <HeaderConfig logo={Logo} />
        <Content className="main-content">{children}</Content>
        <Footer />
    </Layout>
  );
};
export default withRouter(ConfigLayout);
