import React from "react";
import {
  BrowserRouter as Router,
  Link,
  Switch,
  withRouter
} from "react-router-dom";
import { RouteWithSubRoutes } from "./../../utils/Helper";
import qs from "qs";
import { Layout, Menu, Icon } from "antd";
/* Import Custom Components */
import "./cpanelLayout.scss";
import Header from "../../components/header/panel/panelHeader";
import Footer from "../../components/footer/Footer";

const { Content, Sider } = Layout;
const LogoLarge = require("../../resources/images/logo.svg");
const LogoSmall = require("../../resources/images/logo-recostado.svg");

const cpanelLayout = props => {
  let { className, location, routes } = props;
  let { search, pathname } = location;
  let query = qs.parse(search.replace("?", ""));
  let theme =
    query.theme !== "dark" && query.theme !== "light" ? "light" : query.theme;

  return (
    <Router>
      <Layout className={`main-layout ${className || ""} ${theme}`}>
        <Sider collapsible className="side-bar">
          <div className="logo">
            <img id={"logo-large"} src={LogoLarge} />
            <img id={"logo-small"} src={LogoSmall} />
            <span className="logo-title">DonecLab</span>
          </div>
          <Menu
            className="menu-sider"
            defaultSelectedKeys={[pathname]}
            mode="inline"
          >
            <Menu.Item key="/cpanel/dashboard">
              <Icon type="dashboard" />
              <Link to={"/cpanel/dashboard"}>Dashboard</Link>
            </Menu.Item>
            <Menu.Item key="/cpanel/collections">
              <Icon type="database" />
              <Link to={"/cpanel/collections"}>Collections</Link>
            </Menu.Item>
            <Menu.Item key="/cpanel/plugins">
              <Icon type="api" />
              <Link to={"/cpanel/plugins"}>Plugins</Link>
            </Menu.Item>
            <Menu.Item key="/cpanel/connection">
              <Icon type="rise" />
              <Link to={"/cpanel/connection"}>Connection</Link>
            </Menu.Item>
            <Menu.Item key="/cpanel/settings">
              <Icon type="setting" />
              <Link to={"/cpanel/settings"}>Settings</Link>
            </Menu.Item>
          </Menu>
        </Sider>
        <Layout>
          <Header />
          <Content className="main-content">
            <Switch>
              {routes.map((route, i) => (
                <RouteWithSubRoutes key={i} {...route} />
              ))}
            </Switch>
          </Content>
          <Footer />
        </Layout>
      </Layout>
    </Router>
  );
};
export default withRouter(cpanelLayout);
