import React, { Component } from "react";
import { withRouter, Link } from "react-router-dom";
import Form from "../com/form/AdvancedForm";
import { Row, Col, Icon, Button, Input, Checkbox, message } from "antd";

import { login, list_frias } from "../../services";
class LoginForm extends Component {
  onSubmit = (err, data) => {
    let { history, onSubmit } = this.props;
    if (err) return message.error(err);

    login(data)
      .then(response => {
        let { data } = response;
        let { success, token } = data;
        /* if (success) history.push("/cpanel");
        localStorage.setItem("token", token); */
        if (success)
          list_frias()
            .then(response => console.log("FRIAS: ", response.data))
            .catch(err => console.log("ERROR: ", err));
        if (typeof onSubmit !== "undefined") onSubmit(data);
      })
      .catch(err => message.error(err.message));
  };
  render() {
    return (
      <Form
        style={{
          width: "100%"
        }}
        formLayout="vertical"
        onSubmit={this.onSubmit}
        footer={
          <Row type="flex" justify="center" align="middle">
            <Col span={24}>
              <Button
                type="primary"
                shape="round"
                icon="download"
                htmlType="submit"
              >
                Iniciar
              </Button>
            </Col>
          </Row>
        }
      >
        <div>
          <Input
            name="email"
            style={{
              width: "100%"
            }}
            validator={[
              {
                required: true,
                message: "Email is required"
              }
            ]}
            placeholder="Email o Correo electrónico "
            suffix={<Icon type="user" style={{ color: "rgba(0,0,0,.25)" }} />}
          />
          <Input.Password
            style={{
              width: "100%"
            }}
            name="password"
            validator={[
              {
                required: true,
                message: "Password is required"
              }
            ]}
            placeholder="Password o Contraseña"
          />
          <Checkbox
            style={{
              width: "100%"
            }}
            onChange={this.onChange}
          >
            <span
              style={{
                width: "100%"
              }}
            >
              <strong>Recordar usuario</strong> <br />
            </span>
          </Checkbox>
          <Row
            type="flex"
            justify="start"
            align="middle"
            style={{
              width: "100%"
            }}
          >
            <span className={"text-purple"} style={{ marginLeft: 20 }}>
              <Link to="/resetpassword"> ¿Olvidaste tu contraseña? </Link>
            </span>
          </Row>
        </div>
      </Form>
    );
  }
}
export default withRouter(LoginForm);
