const codeAdd = (props) =>{
    let {location} = props;
    let { search } = location;
    let query = qs.parse(search.replace("?", ""));
    let collection = (query.collection == '' || query.collection == undefined) ? "Product" : query.collection ;
    const history = props.history;
    console.log("(Collection: ", query.collection);
    return (
      <div className="home-container">
        <Row>
          <div style={{
            padding: "20px 10px",
            display: 'flex'
          }}>
            <Col span={24}>
              <Button 
                onClick={event => {
                  // If you don't want click extra trigger collapse, you can prevent this:
                  event.stopPropagation();
                  buttonToHistory(history, '/cpanel/collections');
                }}
                className={'chip chip-fill'} type="primary" icon="database">
                Collections
              </Button>
              <Icon className="breadcrumb" type="right" />
              <Button 
                onClick={event => {
                  // If you don't want click extra trigger collapse, you can prevent this:
                  event.stopPropagation();
                  buttonToHistory(history, '/cpanel/collection/structure');
                }}
                className={'chip chip-fill'} type="primary" icon="setting">
                {collection}
              </Button>
              <Icon className="breadcrumb" type="right" />
              <Button className={'chip chip-fill'} type="primary" icon="profile">
                {collection}
              </Button>
            </Col>
          </div>
        </Row>
        <Row>
        <Collapse
            className={'collapse-collections'}
            defaultActiveKey={['0']}
            onChange={callback}
            expandIconPosition={'right'}
          >
            <Panel key={0} header={[
                  <Icon className={'icon-collapse'} type="database" />,
                  <div className={'title-collapse'}
                        style={{
                          marginLeft: 0,
                          padding:"10px 0"}}>
                    <strong>New: <span style={{fontWeight: 300}}>{collection}</span></strong>
                    <span>
                      <i>/App/{collection} </i>
                      </span>
                  </div>
                  ]} extra={genExtra()}>
                  <div>Hola mama</div>
            </Panel>
            
          </Collapse>
          <br />
          
        </Row>
      </div>
    );
}
/* Mapping Actions to Props*/
const mapStateToProps = state => {
  const { data } = state;
  return {
    data
  };
};
/* Event Action Dispatch to Props */
const mapDispatchToProps = dispatch => ({
  actions: bindActionCreators(actions, dispatch)
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(codeAdd);
