import React, { Component } from "react";
/* Redux Connect */
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import actions from "../../redux/actions";

/* Import Custom Components */
import Layout from "../../layouts/config/configLayout";
import { Row, Col, Icon, Button } from 'antd';

const itemCard =[
  {name: 'ChocoOnline', user: 'Elkin Portocarrero'},
  {name: 'DinamiPark', user: 'Elkin Portocarrero'},
  {name: 'TrabajoOnline', user: 'Elkin Portocarrero'},
  {name: 'Camella', user: 'Elkin Portocarrero'}

];
class Config extends Component {
  componentDidMount() {
    const { actions } = this.props;
    const theme = this.props.match.params.theme;
    (theme !== 'light' && theme !== 'dark') ? this.themeColor = 'light' : this.themeColor = theme;
    actions.action_search(["1", "2", "3"]);
  }
  componentWillReceiveProps(nextProps) {
    let { data } = nextProps;
    console.log("Example Reducers Data: ", data);
  }
  render() {

    return (
      <Layout themeColor={this.themeColor}>
        <div className="config-container">
          <div className="config-title">
            <p>Proyectos Donec</p>
          </div>
          <div>
            <Row>
            <Col xs={24} sm={24} md={6} lg={6}>
              <Button className={'card-button'} type="primary" icon="plus">
                New Database
              </Button>
              <Button className={'card-button'} type="primary" icon="database">
                DEMO DATA
              </Button>
              
            </Col>
            <Col xs={24} sm={24} md={18} lg={18}>
            <div className="card-container">
              <Row>
                {
                  itemCard.map(item => (
                    <div className={'card-button'} type="primary" icon="plus" key={itemCard.indexOf(item)}>
                      <div className={'card-container'}>
                        <Icon type="database" />
                        <p className={'card-title'}>
                            {item.name}
                        </p>
                      </div>
                      <div className={'card-footer'}>
                        <div className={'footer-row'} ><Icon type="calendar" />12 de Enero del 2019</div>
                        <div className={'footer-row'} ><Icon type="user" />{item.user}</div>
                      </div>
                    </div>
                  ))
                }
              </Row>
            </div>
            </Col>
            </Row>
          </div>
        </div>
      </Layout>
    );
  }
}
/* Mapping Actions to Props*/
const mapStateToProps = state => {
  const { data } = state;
  return {
    data
  };
};
/* Event Action Dispatch to Props */
const mapDispatchToProps = dispatch => ({
  actions: bindActionCreators(actions, dispatch)
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Config);
